@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
/* Base Styling  */
body {
  --bg-color: rgb(41, 53, 63);
  --bg-color2: rgb(30, 36, 45);
  --text-color: rgb(255, 255, 255);
  background-color: var(--bg-color);
  color: var(--text-color);
  padding: 0;
  box-sizing: border-box;
  margin: 0;
  font-family: "Open Sans";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  /* Give links a code font */
  text-decoration: none;
  color: var(--text-color);
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Layout Styling  */
.page-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;

  font-size: 1.25rem;
  min-height: 80vh;
}
.page-container > div {
  width: 80%;
}
.info-box {
  border-radius: 2rem;
  background-color: var(--bg-color2);
  padding: 1.5rem;
  transition: ease 0.5s;
}
.info-box:hover {
  transform: scale(1.03);
  transform-origin: 50%, 50%;
}
.info-box:hover img:hover {
  transform: scale(1.025);
  transform-origin: 50%, 50%;
}

/** Content Styling **/
h1,
h2,
h3 {
  font-family: Montserrat;
}
hr {
  width: auto;
  height: 0;
  border: 0.2rem solid var(--bg-color2);
  border-radius: 15px;
}
ul {
  padding-left: 1.5rem;
}
img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  height: 250px;
  max-width: min(100%, 25rem);
  border-radius: 2rem;
  transition: ease-out 0.4s;
}

/* Home Page Stuff */
.languages {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
  font-style: italic;
}

div.navbar {
  --navbar-height: min(15vw, 4rem);

  width: 100%;
  height: var(--navbar-height);
  background-color: var(--bg-color2);
}

a.navLink {
  font-size: calc(min(10vw, 3rem) / 2);
}

nav.nav-elements {
  display: flex;
  height: 100%;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}
.navLink {
  height: 100%;
  transition: ease 0.05s;
}
.navLink:hover {
  background-color: var(--bg-color);
}
.nav-element {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;

  margin-left: 5vw;
  margin-right: 5vw;
  transition: ease-out 0.4s;
}
.navLink:hover .nav-element {
  transform: translate(0, -0.2rem);
}

#footer {
  right: 0;
  bottom: 0;
  left: 0;
  height: auto;
  text-align: center;
}

#footer > nav {
  padding: 2rem;
}

a.social-button {
  margin: 1rem;
  padding: 0.75rem;
  border-radius: 20px;
  max-width: 5rem;
  background-color: var(--bg-color2);
  border: 1px solid var(--bg-color2);
  transition: 0.2s;
}
a.social-button:hover {
  background-color: var(--bg-color);
  border: 3px solid var(--bg-color2);
}

